@font-face {
    font-family: Without;
    src: url(/fonts/WithoutAltSans-Black.ttf);
}

@font-face {
    font-family: Droid;
    src: url(/fonts/DroidSerif.ttf);
}

@font-face {
    font-family:Monserrat ;
    src: url(/fonts/Montserrat-SemiBold.otf);
}

body{
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

//css for first-part
.first-part{
    position: relative;
    width: 100vw;
    height: 100vh; /* Full viewport height */
    overflow: hidden;
    margin: 0;
    padding: 0;
}

.background-video {
    position: absolute;
    top: 50vh;
    left: 50vw;
    width: 100vw;
    height: 100vh;
    object-fit: cover; /* Ensures the video covers the entire div */
    transform: translate(-50%, -50%);
    z-index: -1; 
    pointer-events: none; 
    filter: brightness(0.6);
}

.first-part .deel1 {
    position: relative;
    z-index: 1; /* Bring the content to the top */
}


//css for deel
.deel1{
    display: flex;
    flex-direction: row;
    width: 100vw;
    align-items: center;
    justify-content: space-around;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin-top: 2vh;
    margin: 0;
    padding: 0;
}

.deel1 a{
    text-decoration: none;
    color:#013335 ;
}

.logo{
    width: 100vw;
    display: flex;
    justify-content: center;
    margin-top: 2vh;
}

.logo img{
    width: 3vw;
}


.nav{
    display: flex;
    flex-direction: row;
    width: 100vw;
    justify-content: center;
    margin-top: 2vh;
}

.nav .main{
    font-weight: bolder;
    font-family: Monserrat;
    color: white;
}



.dropdown {
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 20vw;
    position: relative; /* Important for positioning dropdown-content */
    transition: all 1s ease-in-out;
  }

  
  .dropdown-content {
    display: flex;
    flex-direction: column;
    margin-top: 3vh;
    visibility: hidden;
    opacity: 0; /* Ensure initial opacity is 0 */
    position: absolute; /* Position relative to dropdown */
    top: 100%; /* Position below dropdown */
    left: 5vw;
    width: 10vw;
    background-color: whitesmoke; /* Optional: Set background color */
    transition: all 1s ease-in-out;
  }

  .dropdown-content a{
    padding: 5px;
    transition: all 1s ease-in-out;
  }

  .dropdown-content a:hover{
    background-color: grey;
  }
  
  .dropdown:hover .dropdown-content {
    visibility: visible;
    opacity: 1; /* Show dropdown content on hover */
  }

.phone-links{
   
    display: none;
}

.phone-links .main{
    font-family: Monserrat;
    transition: all 1s ease-in;
}

.phone-links .main:hover{
    background-color: goldenrod;
}

.hamburger-class{
    width: 100vw;
    display: none;
    font-size:2em ;
    transition: all 1s ease-in;
    color: white;
}



.phone-links-add{
    display: flex;
    flex-direction: column;
    background-color: whitesmoke;
    width: 35vw;
    text-align: left;
    padding-bottom: 2vw;
}

.phone-links a{
    margin-top: 2vh;
    transition: background-color 2s ease-in;
    padding-left: 2vw;
    padding-top: 0.5vh;
    padding-bottom: 0.5vh;
    padding-right: 2vw;
    font-size: medium;
}

.phone-links a:hover{
    background-color: grey;
}


@media (max-width : 1000px) {
    body{
        margin: 0;
        padding: 0;
    }

    .background-video {
        object-fit: cover; 
        width: 100vw; 
        height: 100vh; 
        left: 49.9vw;
    }

    .deel1{
        display: flex;
        justify-content: space-around;
        align-items: flex-start;
        margin-top: 2vh;
    }
    .logo img{
        width: 10vw;
    }
    .nav{
        display: none;
    }
    .hamburger-class{
        display: block;
        margin-left: 20%;
    }
}



.swipe-in{
    transform: translateX(100%);
    animation: swipe-in 1s ease-in-out forwards;
}

.swipe-out{
    transform: translateX(0%);
    animation: swipe-out 1s ease-out forwards;
}

@keyframes swipe-in {
    from{
        opacity: 0;
        transform: translateX(100%);
    }

    to{
        opacity: 1;
        transform: translateX(0%);
    }
}

@keyframes swipe-out {
    from{
        opacity: 1;
        transform: translateX(0%);
    }

    to{
        opacity: 0;
        transform: translateX(100%);
    }
}

@keyframes fade-in {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

.fade-in{
    animation: fade-in 1s ease-in forwards;
}

.deel2{
    display: flex;
    flex-direction: column;
    width: 100vw;
    text-align: center;
    margin-top: 10vh;
    font-size: larger;
    color: white;
    align-items: center;
    justify-content: center;
}

.deel2 .welcome{
    font-family: droid;
    font-style: italic;
    color: gold;
}

.deel2 h1{
    font-size: 4em;
    font-family: Without;
    margin-top: -1vh;
}

.deel2 .therefore{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    padding-left: 3vw;
    padding-right: 3vw;
    margin-top: -1vh;
}

.deel2 button{
   display: block;
   width: 10vw;
   padding: 0.4vw;
   border: none;
   background-color: whitesmoke;
   font-family: without;
   color: #013335;
   transition: color 1s ease-in,background-color 1s ease-in;
}

.deel2 button:hover{
    background-color: #013335;
    color: whitesmoke;
}

@media (max-width : 1000px){
    .deel2{
        font-size: medium;
    }

    .deel2 button{
        width: 40vw;
        padding: 2vw;
    }
    .hamburger-class{
        margin-top: 2vh;
    }
}

// css for the div
.photo-with-links{
    display: flex;
    width: 100vw;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 4%;
    
}

.youth{
    display: flex;
    flex-direction: column;
    text-align: center; 
}

.youth a{
    margin-top: -15vh;
    text-decoration: none;
    font-family: without;
    color: white;
    text-shadow: 1px 1px 1px black;
    z-index: 77;
}

.photo-with-links .youth img{
    width: 30vw;
    filter: brightness(0.3);
    transition: all 1s ease-in;
}

.youth img:hover{
  filter: brightness(0.8);
}



@media (max-width : 1000px){

    .photo-with-links{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 4vh;
    }

    .youth{
        display: flex;
        flex-direction: column;
        position: relative;
        margin-bottom: 10vh;
    }

    .photo-with-links .youth img{
        width: 100vw;
    }
    
}


